@import "shared";

/* common styles */
body {
font-family: arial;
color: #fff;
font-size: 10px;
text-align: center;
background-color: #000;
}

a:link {
color: #fff;
text-decoration: none;
}

a:visited {
color: #fff;
text-decoration: none;
}

a:hover {
color: #fff;
text-decoration: underline;
}

a.active {
color: #7D5C29;
font-weight: bold;
}

a:link.active {
color: #7D5C29;
font-weight: bold;
}

a:visited.active {
color: #7D5C29;
font-weight: bold;
}

h3 {
background-color: #000;
color: #DCD4B3;
font-weight: bold;
font-size: 11pt;
padding: 5px 5px 5px 15px;
text-transform: uppercase;
margin-bottom: 5px;
}

h4 {
margin: 0 0 3px 16px;
color: #DCD4B3;
font-size: 12px;
text-transform: uppercase;
}

ul {
color: #DCD4B3;
margin-top: 3px;
margin-bottom: 8px;
margin-left: 30px;
margin-right: 10px;
}

html > body ul {
margin-left: -7px;
}

li {
list-style-image: url(/crossing_preview/images/arrow.gif);
margin-bottom: 3px;
vertical-align: top;
}

table {

}

.table2 {
font-size: 12px;
padding: 0;
margin: 0;
cellpadding: 0;
cellspacing: 0;
background-image: none;
}

.tr2 {
margin: 0;
padding: 0;
}

.td2 {
width: 160px;
padding: 0;
margin: 0;
}

select {
font-size: 11px;
font-weight: bold;
width: 225px;
background-color: #DCD4B3;
border: 0px;
margin: 0px;
}

/* header */
.header {
max-width: 750px;
height: 48px;
background-image: url(/crossing_preview/images/header.jpg);
margin-bottom: 7px;
text-transform: uppercase;
font-weight: bold;
background-repeat: no-repeat;
text-align: left;
}

.header a:link {
color: #b29e83;
text-decoration: none;
}

.header a:visited {
color: #b29e83;
text-decoration: none;
}

.header a:hover {
color: #b29e83;
text-decoration: none;
}

/* flash container */

/* day bins */

.binHeader {
font-family: impact;
font-size: 21px;
font-weight: normal;
color: #7d5c29;
margin-bottom: -7px;
}

.binImage {
float: left;
padding-left: 5px;
padding-right: 5px;
width: 100px;
}

/* footer */
.footer {
text-align: left;
color: #b29e83;
display: inline-block;
width: 750px;
min-height: 50px;
background-image: url(/crossing_preview/images/footer.jpg);
background-size: cover;
margin-bottom: 10px;
padding: 10px 10px 10px 10px;
background-repeat: no-repeat;
box-sizing: border-box;
}

@media only screen and (max-width: $mobile-breakpoint) {
	.footer {
	    width: 100%;
	}
}

.footer p {
margin-top: 0px;
margin-bottom: 0px;
}

.footer a:link {
color: #b29e83;
font-weight: bold;
text-decoration: none;
}

.footer a:visited {
color: #b29e83;
font-weight: bold;
text-decoration: none;
}

.footer a:hover {
color: #b29e83;
font-weight: bold;
text-decoration: underline;
}

@font-face {
font-family: Arial;
font-style:   normal;
font-weight: normal;
src:url(http://www.SiteAddress.htm/EOTfileName.eot);
}

.partnav {
width: 750px;
color: #b29e83;
margin-bottom: 5px;
font-size: 10pt;
}

/* right rail */
.rightRail {
width: 350px;
margin: 0 0 0 5px;
float: right;
}

/* photo bucket */
.photoBucket {
font-size: 8pt;
text-align: right;
}

#photoBucketImage {
	max-width: 100%;
}

#photoBucketImageContainer {
	display: block;
	margin-left: auto;
	max-width: 350px;
}

.photoBucketCaption {
text-align: left;
font-size: inherit;
}

/* story tools */
.railBin {
margin-top: 10px;
background-image: url(/crossing_preview/images/rightbg.jpg);
background-color: #a5937d;
repeat: no-repeat;
padding-bottom: 10px;
}

.railBin p {
margin-right: 10px;
}
